import React, { Component } from 'react'
import Image from '../../common/Image.js'
import s from './News.css'

class CardContainer extends Component {
  render () {
    return (
      <div className={s.cardContainer + ' ' + this.props.className}>
        {this.props.children}
      </div>
    )
  }
}

class Card extends Component {
  render () {
    return (
      <CardContainer className={s.card}>
        <div className={s.newsTitle}>TFBOYS易烊千玺 发微博了</div>
        <div className={s.newsSpan}>
          <span>2018-08-19 </span>
          <span> 20:12</span>
        </div>
        <div className={s.newsContent}>#粽子节#晒粽子，今年也一起过端午吧</div>
        <div className={s.newsPicContainer}>
          {[1, 2, 2, 2, 2, 2, 2, 2, 2].map((item, index) => (
            <Image className={s.newsPic} key={index} src="http://ent.cntv.cn/20110510/images/1304991303182_50361554.jpg"></Image>
          ))}
        </div>
      </CardContainer>
    )
  }
}

class News extends Component {
  render () {
    return (
      <div className={s.news}>
        <CardContainer className={s.scheduleContainer}>
          <div className={s.schedule}>下个行程：8月3日 21:00</div>
          <div className={s.destination}>出席上海杜莎夫人蜡像馆</div>
        </CardContainer>
        <Card></Card>
        <Card></Card>
        <Card></Card>
        <Card></Card>
        <Card></Card>
        <Card></Card>
      </div>
    )
  }
}

export default News



