import React, { Component } from 'react'
import { Route, Switch } from 'react-router-dom'
import News from './News'
import Community from './Community'
import Billbord from './Billbord'
import s from './Star.css'
import { IconName } from '../common/AppFooter.js'
import { connect } from 'react-redux'
import actions from '../../actions'

class NavCore extends Component {
  render () {
    let { activeStarId } = this.props
    return (
      <div className={s.nav + ' ' + this.props.className} style={this.props.style}>
        <IconName to={`/star/${activeStarId}/community`} icon="icon-shequ-active" title="社区"></IconName>
        <IconName to={`/star/${activeStarId}/news`} icon="icon-icon-xiaoxi" title="动态"></IconName>
        <IconName to={`/star/${activeStarId}/billbord`} icon="icon-paihangbang" title="打榜"></IconName>
      </div>
    )
  }
}

function navMapStateToProps ({ activeStar }, ownProps) {
  return {
    ...ownProps,
    activeStarId: activeStar.star_id
  }
}
const Nav = connect(navMapStateToProps, actions.activeStar)(NavCore)

class SorptionNav extends Component {
  constructor (props) {
    super(props)
    this.state = {
      sorption: false
    }

    this.handleScroll = this.handleScroll.bind(this)
  }
  handleScroll () {
    let sorption = window.document.documentElement.scrollTop > this.props.top
    this.state.sorption !== sorption && this.setState({ sorption })
  }
  componentDidMount () {
    window.addEventListener('scroll', this.handleScroll)
  }
  componentWillUnmount () {
    window.removeEventListener('scroll', this.handleScroll)
  }
  render () {
    return (
      <React.Fragment>
        <Nav className={s.sorptionNav} style={{opacity: this.state.sorption ? 1 : 0}}></Nav>
      </React.Fragment>
    )
  }
}

class Star extends Component {
  constructor (props) {
    super(props)
    this.state = {
      bannerHeight: 1000
    }

    this.handleLoad = this.handleLoad.bind(this)
  }
  handleLoad (e) {
    this.setState({
      bannerHeight: e.target.height
    })
  }
  render () {
    let div = document.createElement('div')
    let canUseSticky = false
    if (div.style.position !== undefined) {
      div.style.position = 'sticky'
      if (div.style.position === 'sticky') {
        canUseSticky = true
      } else {
        div.style.position = '-webkit-sticky'
        if (div.style.position === '-webkit-sticky') {
          canUseSticky = true
        }
      }
    }

    return (
      <div className={s.star}>
        {!canUseSticky && <SorptionNav top={this.state.bannerHeight}></SorptionNav>}
        <img className={s.banner} onLoad={this.handleLoad} src="http://sohu-media.bjcnc.scs.sohucs.com/pickme/yiyangqianxi.jpg " alt=""/>
        <Nav className={s.desc}></Nav>
        <Switch>
          <Route exact path="/star/:star_id/news" component={News} />
          <Route path="/star/:star_id/community" component={Community} />
          <Route path="/star/:star_id/billbord" component={Billbord} />
        </Switch>
      </div>
    )
  }
}

export default Star



